<template>
	
	<view class="wrap">
		<u-swipe-action :show="item.show" :index="index" v-for="(item, index) in addressList" :key="item.id"
			@click="click" @open="open" :options="options" btn-width="240" v-if="index<1">
			<view class="item u-border-bottom u-p-20">
				<view class="top">
					<view class="name">{{ item.name }}</view>
					<view class="phone">{{ item.phone }}</view>
					<view class="tag" v-if="item.is_default === 1">
						<text class="red">默认</text>
					</view>
				</view>
				<view class="bottom">
					
					{{item.province}}{{item.city}}{{item.county}}{{item.address}}
					<u-icon name="edit-pen" :size="40" color="#999999" @click="toAddSite(item.id)"></u-icon>
				</view>
			</view>
		</u-swipe-action>
		<template v-if="vuex_token">
			<view v-if="goodsList.length" class="cartGoodsList u-p-b-80">
				<view class="cartGoods u-flex u-p-15" v-for="goods in goodsList" :key="goods.id">
					<view>
						<u-checkbox v-model="goods.is_checked?true:false" :name="goods.id" @change="checkChange"
							size="40" shape="circle"></u-checkbox>
					</view>
					<view class="goodsInfo u-flex u-p-10">
						<view @tap='jump(`/pages/goods/goods-detail/goods-detail`,goods.goods_id)'>
							<u-image border-radius="15" width="200rpx" height="200rpx" :src="goods.goods.cover_url">
							</u-image>
						</view>
						<view class="goodsBaseInfo u-flex u-row-between u-col-top u-p-10">
							<view class="priceNum" @tap='jump(`/pages/goods/goods-detail/goods-detail`,goods.goods_id)'>
								<text class="title u-p-l-10 u-font-32">{{goods.goods.title}}</text>
								<br>
								<text class="small">{{goods.goods.description}}</text>
								<view class="u-text-right small">
									库存:<text style="color:red">{{goods.goods.stock}}</text>
								</view>
							</view>
							<view class="priceNum u-flex u-row-between">
								<text class="price u-font-34">￥{{goods.goods.price}}</text>
								<u-number-box v-model="goods.num" :min="1" :max="goods.goods.stock" :index="goods.id"
									@change="numChange"></u-number-box>
								<u-icon @tap="removeGoods(goods.id)" name="trash" color="#e83333" size="36">
								</u-icon>
							</view>
						</view>
					</view>
				</view>
			</view>
			<view class="empty" v-else>
				<u-empty text="购物车空空如也" mode="car"></u-empty>
			</view>
			<view class="bottomFixed u-flex u-p-15">
				<view class="u-flex-5">
					<u-checkbox v-model="allCheck" size="40" shape="circle">全选</u-checkbox>
				</view>
				<view class="u-flex u-flex-20 u-row-around">
					<view>
						<text class="title">合计:</text>
						<text class="price">￥{{totalPrice}}</text>
					</view>
					<u-button type="primary" :ripple="true" shape="square" @click="pay" >提交订单</u-button>
				</view>
			</view>
		</template>
		<template v-else>
			
			<u-empty text="暂未登入" mode="permission"></u-empty>
		</template>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				addressList: [], // 地址列表
				goodsList: [],
				num:1,
				orders:[],
				kk:0,
				addrsid:0,
				options: [{
						text: '设为默认',
						style: {
							backgroundColor: '#007aff'
						}
					},
					{
						text: '删除',
						style: {
							backgroundColor: '#dd524d'
						}
					}
				]
			}
		},
		onShow() {
			if (this.vuex_token) {
				this.getCartGoods()
			}
		},
		computed: {
			// 全选和全不选判断
			allCheck: {
				get() {
					return this.goodsList.every((goods) => {
						return goods.is_checked
					})
				},
				set(val) {
					this.checkApi(val)
				}
			},
			// 选中商品的总价
			totalPrice() {
				return this.goodsList.filter((goods) => { //过滤掉没有选中商品
					if (goods.is_checked) {
						return true
					}
				}).reduce((pre, goods) => { //价格累加
				   this.kk = (parseInt(pre) + parseInt(goods.goods.price) * goods.num)
					return parseInt(pre) + parseInt(goods.goods.price) * goods.num //乘商品数量
				}, 0)
			}
		},
		onLoad() {
			this.getData();
		},
		methods: {
			async getData() {
				const res = await this.$u.api.addressList()
				
				this.addressList = res.data.map(item => {
					item.show = false
					return item;
				});
			},
			
			// 商品详情跳转
			jump(url, id) {
				this.$u.route({
					url,
					params: {
						id
					}
				})
			},
			// 获取购物车内商品
			async getCartGoods() {
				const {
					data
				} = await this.$u.api.cartGoods()
				this.goodsList = data
			
			},
			// 商品数量改变
			async numChange(val) {
				// 通过设置index获取（购物车商品的id）既val中第二参数
				const {
					value,
					index
				} = val
				await this.$u.api.numChange(index, String(value))
			},
			// 移出购物车
			async removeGoods(id) {
				// delCartGoods
				await this.$u.api.delCartGoods(id)
				// 删除商品后进行提示   再次调用获取商品列表
				this.$u.toast("移出购物车成功")
				this.getCartGoods()
			},
			// 单个商品是否选中
			async checkChange(val) {
				const {
					value,
					name
				} = val
				let idArr = [];
				// 先把已经选中的商品id存入idArr内
				this.goodsList.forEach((goods) => {
					if (goods.is_checked) {
						idArr.push(goods.id)
					}
				})
				if (!value) {
					//选中状态，点击后变成false
					idArr.splice(idArr.indexOf(name), 1)
					await this.$u.api.isCheck(idArr)
				} else {
					//没选中中状态，点击后变成true
					idArr.push(name)
					await this.$u.api.isCheck(idArr)
				}
				// 最后重新调用获取商品数据更新
				this.getCartGoods()
			},
			// 全选与全不选接口调用
			async checkApi(val) {
				// 准备获取各个购物车id参数
				let idArr = []
				if (!val) {
					// 全选点击（变全不选）
					idArr = []
					await this.$u.api.isCheck(idArr)
				} else {
					// 全不选选点击（变全选）
					this.goodsList.forEach((goods) => {
						idArr.push(goods.id)
					})
					await this.$u.api.isCheck(idArr)
				}
				// 最后重新调用获取商品数据更新
				this.getCartGoods()
			},
			// 地址添加或修改
			toAddSite(id) {
				// 判断是添加地址还是修改地址
				console.log(id)
				this.$u.route({
					url: 'pages/cart/addAddress',
					params: {
						addressId: id
					}
				})
			},
			// 支付
			async pay(){
			  const params = {
					address_id:this.addressList[0].id,
			  }
			  
			  let aa = await this.$u.api.orders(params);
			  console.log(aa)
			  this.$u.toast('请稍后....')
			  setTimeout(() => {
			  	this.$u.route({
					url:'pages/cart/pay/pay?order_no='+aa.order_no+"&amount="+aa.amount+"&title="+123,
					type:'reLaunch'
				});
			  },1000)
			}
		}
	}
</script>

<style lang="scss" scoped>
  .item {
  	padding: 40rpx 20rpx;
  
  	.top {
  		display: flex;
  		font-weight: bold;
  		font-size: 34rpx;
  
  		.phone {
  			margin-left: 60rpx;
  		}
  
  		.tag {
  			display: flex;
  			font-weight: normal;
  			align-items: center;
  
  			text {
  				display: block;
  				width: 60rpx;
  				height: 34rpx;
  				line-height: 34rpx;
  				color: #ffffff;
  				font-size: 20rpx;
  				border-radius: 6rpx;
  				text-align: center;
  				margin-left: 30rpx;
  				background-color: rgb(49, 145, 253);
  			}
  
  			.red {
  				background-color: red
  			}
  		}
  	}
	.bottom {
  		display: flex;
  		margin-top: 20rpx;
  		font-size: 28rpx;
  		justify-content: space-between;
  		color: #999999;
  	}
  }
  
  .addSite {
  	display: flex;
  	justify-content: space-around;
  	width: 600rpx;
  	line-height: 100rpx;
  	position: absolute;
  	bottom: 30rpx;
  	left: 80rpx;
  	background-color: red;
  	border-radius: 60rpx;
  	font-size: 30rpx;
  
  	.add {
  		display: flex;
  		align-items: center;
  		color: #ffffff;
  
  		.icon {
  			margin-right: 10rpx;
  		}
  	}
  }
  .wrap {
  	min-height: 80vh;
  	.empty {
  		height: 80vh;
  	}
  
  	.loading {
  		text-align: center;
  		position: absolute;
  		top: 50%;
  		left: 50%;
  		transform: translate(-50%, -50%);
  	}
  
  	.title,
  	.price {
  		font-weight: 800;
  	}
  
  	.price {
  		color: #e83333;
		margin-right: 20px;
  	}
  
  	.cartGoodsList {
  		.cartGoods {
  			.goodsInfo {
  				width: 100%;
  				background-color: rgb(251, 250, 251);
  
  				.goodsBaseInfo {
  					width: 100%;
  					height: 200rpx;
  					flex-direction: column;
  
  					.priceNum {
  						width: 100%;
  
  					}
  				}
  			}
  		}
  	}
  
  	.bottomFixed {
  		position: fixed;
  		left: 0;
  		right: 0;
  		background-color: #fff;
  	}
  
  	/*#ifdef H5*/
  	.bottomFixed {
  		bottom: 100rpx;
  	}
  
  	/*#endif*/
  	/*#ifdef MP-WEIXIN*/
  	.bottomFixed {
  		bottom: 0;
  		z-index: 99999;
  	}
  
  	/*#endif*/
  
  }
</style>
